// basic styles
body {
	min-width: 320px;
	font-family: 'Open Sans', Arial, sans-serif;
	font-weight: 300;
	font-size: 16px;
	line-height: 1.2;
	color: $black;
	&.is-locked {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		height: 100%;
		overflow: hidden; } }

* {
	-webkit-tap-highlight-color: transparent; }

h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }

a {
	color: inherit;
	text-decoration: none;
	&:hover, &:focus {
		text-decoration: none; } }

button {
	background-color: transparent; }

h1, h2, h3, h4, h5, h6, p, a, span, div, img, mark, ul, li {
	&::selection {
		color: $white;
		background-color: $crimson; } }

img {
	vertical-align: top;
	max-width: 100%; }

::-ms-clear,
::-ms-reveal {
	display: none;
	width: 0;
	height: 0; }

input:invalid {
	box-shadow: none; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
	-webkit-appearance: none;
	margin: 0; }

video::-webkit-media-controls-start-playback-button {
	display: none !important; }

.obj-fit {
	width: 100%;
	height: 100%;
	object-fit: cover;
	font-family: 'object-fit: cover;'; }

// layout
.layout {
	display: flex;
	overflow: hidden;
	height: 100vh; }
.container {
	max-width: 1800px;
	margin: 0 auto;
	padding: 0 15px; }

// titles
.title-decor-lg {
	position: relative;
	padding: 4px 0;
	@include f('con', 36);
	color: $crimson;
	text-align: center;
	border-top: 3px solid $crimson;
	border-bottom: 3px solid $crimson;
	&::before, &::after {
		content: '';
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1; }
	&::after {
		top: -45px;
		@include s('title-decor-lg'); }
	span {
		display: inline-block;
		vertical-align: middle;
		padding: 5px 0;
		border-top: 1px solid #725b55;
		border-bottom: 1px solid #725b55; }
	&_double {
		font-size: 21px;
		border-top-width: 1px;
		border-bottom-width: 1px;
		span {
			border-top-color: #d1ac88;
			border-bottom-color: #d1ac88; }
		&::after {
			@include s('title-decor-xs'); }
		&::before {
			@include s('title-decor-xs'); } } }

.title-decor-md {
	@include f('con', 67, 89); }

.title-decor-sm {
	@include f('con', 42);
	color: $crimson;
	text-align: center;
	span {
		position: relative;
		padding: 0 100px;
		&::before, &::after {
			content: '';
			position: absolute;
			top: calc(50% - 16px);
			z-index: 1; }
		&::before {
			left: 0;
			@include s('title-decor-sm'); }
		&::after {
			right: 0;
			transform: rotateY(-180deg);
			@include s('title-decor-sm'); } } }

// buttons
.btn {
	display: inline-block;
	vertical-align: top;
	padding: 0 39px;
	border-radius: 10px;
	transition: background-color .2s;
	&_red {
		height: 38px;
		color: $white;
		background-color: $crimson;
		font-size: 17px;
		line-height: 37px;
		@include os-r;
		@include hover {
			background-color: darken($crimson, 5); } } }
