/* LIBS */
/* Slider */
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400&subset=cyrillic");
@import url("https://fonts.googleapis.com/css?family=Roboto:700&subset=cyrillic");
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* HELPERS */
.icon-arr-smp {
  width: 1.87em;
  height: 1em;
  fill: initial;
}

@font-face {
  font-family: "Bebas-Neue-Bold";
  src: url("../fonts/Bebas-Neue-Bold.woff2") format("woff2"), url("../fonts/Bebas-Neue-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Constantia";
  src: url("../fonts/Constantia.woff2") format("woff2"), url("../fonts/Constantia.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* COMPONENTS */
body {
  min-width: 320px;
  font-family: 'Open Sans', Arial, sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.2;
  color: #262423;
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  overflow: hidden;
}

* {
  -webkit-tap-highlight-color: transparent;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

button {
  background-color: transparent;
}

h1::selection, h2::selection, h3::selection, h4::selection, h5::selection, h6::selection, p::selection, a::selection, span::selection, div::selection, img::selection, mark::selection, ul::selection, li::selection {
  color: #fff;
  background-color: #7b232c;
}

img {
  vertical-align: top;
  max-width: 100%;
}

::-ms-clear,
::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input:invalid {
  box-shadow: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

video::-webkit-media-controls-start-playback-button {
  display: none !important;
}

.obj-fit {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.layout {
  display: flex;
  overflow: hidden;
  height: 100vh;
}

.container {
  max-width: 1800px;
  margin: 0 auto;
  padding: 0 15px;
}

.title-decor-lg {
  position: relative;
  padding: 4px 0;
  font-family: "Constantia", Arial, sans-serif;
  font-weight: normal;
  font-size: 36px;
  color: #7b232c;
  text-align: center;
  border-top: 3px solid #7b232c;
  border-bottom: 3px solid #7b232c;
}

.title-decor-lg::before, .title-decor-lg::after {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.title-decor-lg::after {
  top: -45px;
  background-image: url("../img/sprite.png");
  width: 109px;
  height: 40px;
  background-position: -192px -54px;
}

.title-decor-lg span {
  display: inline-block;
  vertical-align: middle;
  padding: 5px 0;
  border-top: 1px solid #725b55;
  border-bottom: 1px solid #725b55;
}

.title-decor-lg_double {
  font-size: 21px;
  border-top-width: 1px;
  border-bottom-width: 1px;
}

.title-decor-lg_double span {
  border-top-color: #d1ac88;
  border-bottom-color: #d1ac88;
}

.title-decor-lg_double::after {
  background-image: url("../img/sprite.png");
  width: 65px;
  height: 24px;
  background-position: -311px -54px;
}

.title-decor-lg_double::before {
  background-image: url("../img/sprite.png");
  width: 65px;
  height: 24px;
  background-position: -311px -54px;
}

.title-decor-md {
  font-family: "Constantia", Arial, sans-serif;
  font-weight: normal;
  font-size: 67px;
  line-height: 1.32836;
}

.title-decor-sm {
  font-family: "Constantia", Arial, sans-serif;
  font-weight: normal;
  font-size: 42px;
  color: #7b232c;
  text-align: center;
}

.title-decor-sm span {
  position: relative;
  padding: 0 100px;
}

.title-decor-sm span::before, .title-decor-sm span::after {
  content: '';
  position: absolute;
  top: calc(50% - 16px);
  z-index: 1;
}

.title-decor-sm span::before {
  left: 0;
  background-image: url("../img/sprite.png");
  width: 85px;
  height: 32px;
  background-position: -177px -132px;
}

.title-decor-sm span::after {
  right: 0;
  transform: rotateY(-180deg);
  background-image: url("../img/sprite.png");
  width: 85px;
  height: 32px;
  background-position: -177px -132px;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 0 39px;
  border-radius: 10px;
  transition: background-color .2s;
}

.btn_red {
  height: 38px;
  color: #fff;
  background-color: #7b232c;
  font-size: 17px;
  line-height: 37px;
  font-family: 'Open Sans', Arial, sans-serif;
  font-weight: 400;
}

.no-touch .btn_red:hover {
  background-color: #671d25;
}

.header {
  position: relative;
  flex: 0 0 400px;
  overflow: hidden;
  padding: 70px 0 0;
  background: #f0d4aa url("../img/bg/header-decor_bg.png") no-repeat 50% 50%;
  border-right: 3px solid #7b232c;
}

.header::after {
  position: absolute;
  top: calc(50% - 163px);
  right: -24px;
  content: '';
  background-image: url("../img/sprite.png");
  width: 47px;
  height: 327px;
  background-position: 0px 0px;
}

.header__in {
  max-width: 300px;
  margin: 0 auto;
}

.header__logo {
  margin: 0 auto 60px;
}

/* menu
	----------------------------------- */
.menu__list {
  font-family: 'Open Sans', Arial, sans-serif;
  font-weight: 400;
  line-height: 1;
}

.menu__list-item.is-active span {
  border-bottom-color: #7b232c;
  color: #7b232c;
}

.menu__list-item.is-active .menu__list-icon {
  transform: scaleY(-1);
}

.menu__list-link {
  font-size: 18px;
  cursor: pointer;
}

.no-touch .menu__list-link:hover {
  color: #7b232c;
}

.menu__list-link span {
  display: inline-block;
  vertical-align: middle;
  padding-bottom: 3px;
  border-bottom: 2px solid transparent;
}

.menu__list-icon {
  display: inline-block;
  vertical-align: baseline;
  margin-left: 6px;
  fill: #7b232c;
  font-size: 6px;
  transition: transform 0.25s ease-in;
}

.menu__sub-list {
  display: none;
  padding: 30px;
}

.menu__sub-list li {
  position: relative;
  padding-left: 30px;
}

.menu__sub-list li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 2px;
  z-index: 1;
  background-image: url("../img/sprite.png");
  width: 12px;
  height: 9px;
  background-position: -192px -104px;
}

.menu__sub-list li:not(:first-child) {
  margin-top: 10px;
}

.menu__sub-list li a {
  font-size: 16px;
}

.no-touch .menu__sub-list li a:hover {
  color: #7b232c;
}

/* address
	----------------------------------- */
.address {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-family: 'Open Sans', Arial, sans-serif;
  font-weight: 400;
}

.address__item {
  position: relative;
  padding-left: 30px;
}

.address__item::before {
  content: '';
  position: absolute;
  top: 8px;
  left: 0;
}

.address__item_link {
  font-size: 20px;
  line-height: 44px;
}

.address__item_link::before {
  background-image: url("../img/sprite.png");
  width: 23px;
  height: 28px;
  background-position: -305px -233px;
}

.address__item_link span {
  border-bottom: 2px dashed #262423;
  transition: .2s;
}

.no-touch .address__item_link span:hover {
  border-bottom-color: transparent;
}

.address__item_tel {
  font-size: 24px;
  line-height: 39px;
}

.address__item_tel::before {
  background-image: url("../img/sprite.png");
  width: 24px;
  height: 26px;
  background-position: -338px -233px;
}

.address__btn {
  align-self: flex-end;
}

/* logo
	----------------------------------- */
.logo {
  display: block;
}

.logo img {
  display: block;
  margin: 0 auto;
}

/* ===================================
	 nav
	 @mixin ================================== */
.main {
  flex: 1 1 auto;
}

/* tabs
	----------------------------------- */
.tabs__nav {
  display: flex;
  font-size: 0;
}

.tabs__nav-item {
  flex: 1 1 33.33333%;
  height: 57px;
  padding: 0 15px;
  border-radius: 15px 15px 0 0;
  background: #f0d4aa;
  color: #262423;
  line-height: 57px;
  text-align: center;
  font-size: 24px;
  cursor: pointer;
  transition: color .3s, background .3s;
}

.tabs__nav-item:not(:first-child) {
  margin-left: 2px;
}

.tabs__nav-item.is-active {
  color: #fff;
  background: #7b232c;
}

.tabs__content {
  position: relative;
  min-height: 415px;
  padding: 50px;
  border-radius: 0 0 15px 15px;
  border: 1px solid #f0d4aa;
  background-color: #fff;
  z-index: 2;
}

.tabs__content::before, .tabs__content::after {
  content: '';
  position: absolute;
  top: 20px;
  background-image: url("../img/sprite.png");
  width: 47px;
  height: 327px;
  background-position: -57px 0px;
  z-index: 1;
}

.tabs__content::before {
  left: -30px;
}

.tabs__content::after {
  right: 0;
  transform: translateX(-80deg);
}

.tabs__content-title {
  margin-bottom: 35px;
  font-family: "Constantia", Arial, sans-serif;
  font-weight: normal;
  font-size: 24px;
  color: #7b232c;
  text-align: center;
}

.tabs__content-descr:not(:first-child) {
  margin-top: 20px;
}

/* PAGES */
.home {
  background-size: 100% 100%;
}

.how-buy {
  padding: 65px 0 0;
}

.how-buy__container {
  max-width: 1012px;
}

.how-buy__title {
  margin-bottom: 60px;
}
