.main {
	flex: 1 1 auto; }

/* tabs
	----------------------------------- */
.tabs {
	&__nav {
		display: flex;
		font-size: 0;
		&-item {
			flex: 1 1 33.33333%;
			height: 57px;
			padding: 0 15px;
			border-radius: 15px 15px 0 0;
			background: $beige;
			color: $black;
			line-height: 57px;
			text-align: center;
			font-size: 24px;
			cursor: pointer;
			transition: color .3s, background .3s;
			&:not(:first-child) {
				margin-left: 2px; }
			&.is-active {
				color: $white;
				background: $crimson; } } }
	&__content {
		position: relative;
		min-height: 415px;
		padding: 50px;
		border-radius: 0 0 15px 15px;
		border: 1px solid $beige;
		background-color: $white;
		z-index: 2;
		&::before, &::after {
			content: '';
			position: absolute;
			top: 20px;
			@include s('vert-decor');
			z-index: 1; }
		&::before {
			left: -30px; }
		&::after {
			right: 0;
			transform: translateX(-80deg); }
		&-title {
			margin-bottom: 35px;
			@include f('con', 24);
			color: $crimson;
			text-align: center; }
		&-descr {
			&:not(:first-child) {
				margin-top: 20px; } } } }
