@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400&subset=cyrillic');
@import url('https://fonts.googleapis.com/css?family=Roboto:700&subset=cyrillic');
@include font("Bebas-Neue-Bold", Bebas-Neue-Bold);
@include font("Constantia", Constantia);

@mixin f($name, $font-size: null, $line-height: null, $letter-spacing: null) {
	$font-family: null;
	@if $name == "con" {
		$font-family: "Constantia"; }
	@if $name == "beb-b" {
		$font-family: "Bebas-Neue-Bold"; }
	font-family: $font-family, Arial, sans-serif;
	@if $font-family != null {
		font-weight: normal; }
	@if $font-size != null {
		font-size: $font-size * 1px; }
	@if $line-height != null {
		line-height: $line-height/$font-size; }
	@if $letter-spacing != null {
		letter-spacing: ($letter-spacing / 1000) * 1em; } }


@mixin os-l {
	font-family: 'Open Sans', Arial, sans-serif;
	font-weight: 300; }

@mixin os-r {
	font-family: 'Open Sans', Arial, sans-serif;
	font-weight: 400; }

@mixin rob-b {
	font-family: 'Roboto', Arial, sans-serif;
	font-weight: 400; }
